export class ForsViewModel
{
 /// <summary>
///  轉資料表序號
/// <summary>
public exec_table_seq: number;
/// <summary>
///  轉檔群組
/// <summary>
public exec_group: string;
/// <summary>
///  來源結構描述
/// <summary>
public src_schema: string;
/// <summary>
///  來源資料表
/// <summary>
public src_tablename: string;
/// <summary>
///  結構描述
/// <summary>
public schemaname: string;
/// <summary>
///  資料表
/// <summary>
public tablename: string;


/// <summary>
///  執行順序
/// <summary>
public exec_seq: number;
/// <summary>
///  啟用註記
/// <summary>
public is_active: boolean;



/// <summary>
///  預存程序名稱
/// <summary>
public sp_name: string;

/// <summary>
///  日期預存程序名稱
/// <summary>
public date_sp_name: string;
/// <summary>
///  交易註記
/// <summary>
public tran_flag: boolean;

/// <summary>
///  db連線編號
/// <summary>
public db_conn_id: string;
/// <summary>
///  備註說明
/// <summary>
public memo: string;
/// <summary>
///  建立者
/// <summary>
public creator: string;
/// <summary>
///  建立時間
/// <summary>
public create_time: Date;
/// <summary>
///  最後更新者
/// <summary>
public modifier: string;
/// <summary>
///  最後更新時間
/// <summary>
public last_update_time: Date;


/// <summary>
///  db連線字串
/// <summary>
public db_conn_string: string;

/// <summary>
///  啟用說明
/// <summary>
public is_active_desc: string;
/// <summary>
///  啟用顏色
/// <summary>
public is_active_color: string;
/// <summary>
///  啟用顏色
/// <summary>
public is_active_color_right: string;
/// <summary>
///  來源資料表全名
/// <summary>
public src_tablefullname: string;

/// <summary>
///  資料表全名
/// <summary>
public tablefullname: string;

/// <summary>
///  sql語法
/// <summary>
public sql_statement: string;

/// <summary>
///  日期sql語法
/// <summary>
public date_sql_statement: string;

}



