import { ForsServicesProvider } from "../../providers/fors-services/fors-services";
import { GlobalComponent } from "../../components/global/global";
import { ModalController, LoadingController } from "ionic-angular";
import { Component } from "@angular/core";
import { String } from "../../Model/String";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController
} from "ionic-angular";
import { ForsViewModel } from "../../Model/ViewModel/ForsViewModel";
import { ConnServicesProvider } from "../../providers/conn-services/conn-services";

/**
 * Generated class for the SqlModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-fors-add-edit-modal",
  templateUrl: "fors-add-edit-modal.html"
})
export class ForsAddEditModalPage {

  item: ForsViewModel;

  mode: string;
  CanEditBatch: boolean;
  title: string;
  Sql_type: string;
  min_exec_table_seq: number = 40000;
  max_exec_table_seq: number = this.min_exec_table_seq + 9999;
  range_exec_table_seq: string;
  change_mode: string;
  date_sp_help: string = `
  SQL語法說明：
  select 資料日期,最後資料日期
  例如：select convert(varchar(10),last_update_time,120) datadate,max(last_update_time) last_data_date
  from comm.tb_file_exec_log
  where last_update_time>'{last_data_date}'
  group by convert(varchar(10),last_update_time,120)

  可接受變數說明：
  {last_data_date}=取得上次執行最後資料日期參數,
  `;
  sql_help: string = `
  變數說明：
  {data_date}=資料日期參數,
  {data_string}=文字參數,
  {data_int}=整數參數,
  {last_data_date}=取得上次執行最後資料日期參數,
  {exec_group}=轉檔群組,
  {JOB01},{JOB02},{JOB03}=作業迴圈參數,
  {LC01},{LC02},{LC03}=內部檔案迴圈參數,
  {exec_log_seq}=上層傳遞的exec_log_seq,
  {src_path_file}=上層傳遞的完整檔名(含路徑)
  動態執行另一作業方法
  系統會執行create table #job( ssis_job_no varchar(30) not null,job01 varchar(50) null,job02 varchar(50) null,job03 varchar(50) null)，
  可以將資料寫入到#job進行呼叫作業執行。
  範例1：呼叫s1_daily_job
  insert into #job(ssis_job_no)
  values ('s1_daily_job')

  範例2：呼叫s1_daily_job 具有 F 的JOB01參數
  insert into #job(ssis_job_no,job01)
  values ('s1_daily_job','F')
  `;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    private ForsServices: ForsServicesProvider,
    public loadingCtrl: LoadingController,
    public global: GlobalComponent,
    public modalCtrl: ModalController,
    private ConnServices: ConnServicesProvider,
  ) {
    this.range_exec_table_seq = String.Format(
      "{0}-{1}",
      this.min_exec_table_seq,
      this.max_exec_table_seq
    );
    this.mode = navParams.data.mode;
    this.change_mode = navParams.data.change_mode;
    this.item = new ForsViewModel();
    this.item.exec_table_seq=navParams.data.item.exec_table_seq;
    this.item.exec_group=navParams.data.item.exec_group;
    this.item.src_schema=navParams.data.item.src_schema;
    this.item.src_tablename=navParams.data.item.src_tablename;
    this.item.schemaname=navParams.data.item.schemaname;
    this.item.tablename=navParams.data.item.tablename;
    this.item.exec_seq=navParams.data.item.exec_seq;
    this.item.is_active=navParams.data.item.is_active;
    this.item.tran_flag=navParams.data.item.tran_flag;
    this.item.db_conn_id=navParams.data.item.db_conn_id;
    this.item.memo=navParams.data.item.memo;
    this.item.creator=navParams.data.item.creator;
    this.item.create_time=navParams.data.item.create_time;
    this.item.modifier=navParams.data.item.modifier;
    this.item.last_update_time=navParams.data.item.last_update_time;
    this.item.sql_statement=navParams.data.item.sql_statement;
    this.item.db_conn_string=navParams.data.item.db_conn_string;
    this.item.sp_name=navParams.data.item.sp_name;
    this.item.date_sp_name=navParams.data.item.date_sp_name;
    this.item.date_sql_statement=navParams.data.item.date_sql_statement;


    if (this.change_mode === "n") {
      this.get_conn_string("localdb");
    }
    if (this.change_mode === "c") {
      this.get_max_exec_table_seq();
      this.get_max_exec_seq();
    }
    this.CanEditBatch = navParams.data.CanEditBatch;

    this.title = "資料來源迴圈";
  }
  get_conn_string(remote_conn_id: string) {
    this.global.createLoader("取得連線字串中...");
    this.global.loading.present().then(() => {
      this.ConnServices.GetConnAsync(remote_conn_id).subscribe(
        data => {
          if (data.DidError === true) {
            this.global.dismissLoading();
            this.global.showError(data.ErrorMessage);
          } else {
            this.item.db_conn_string =
              data.Model.conn_string;
            this.global.dismissLoading();
          }
        },
        err => {
          this.global.dismissLoading();
          this.global.showError("無法連上WebAPI伺服器-"+err.message);
        }
      );
    });
  }

  SelectConn() {
    let modal = this.modalCtrl.create("ConnSelectModalPage", {
      select_remote_conn_id: this.item.db_conn_id,
      conn_type_key: "065|001"
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.db_conn_id = select_data.remote_conn_id;
      this.item.db_conn_string = select_data.conn_string;
    });

    modal.present();
  }




  SelectSrcTable() {
    let modal = this.modalCtrl.create("TableExtSelectModalPage", {
      select_item: this.item.tablefullname
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.src_schema = select_data.schemaname;
      this.item.src_tablename = select_data.tablename;
      this.item.src_tablefullname = select_data.tablefullname;
    });

    modal.present();
  }
  SelectTable() {
    let modal = this.modalCtrl.create("TableExtSelectModalPage", {
      select_item: this.item.tablefullname
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.schemaname = select_data.schemaname;
      this.item.tablename = select_data.tablename;
      this.item.tablefullname = select_data.tablefullname;
    });

    modal.present();
  }

  get_max_exec_table_seq() {
    this.global.createLoader("取得最大值中...");
    this.global.loading.present().then(() => {
      this.ForsServices.GetMaxExecTableSeqAsync(
        this.item.exec_group,
        this.min_exec_table_seq,
        this.max_exec_table_seq,
      ).subscribe(
        data => {
          if (data.DidError === true) {
            this.global.dismissLoading();
            this.global.showError(data.ErrorMessage);
          } else {
            this.item.exec_table_seq = data.Model;
            this.global.dismissLoading();
          }
        },
        err => {
          this.global.dismissLoading();
          this.global.showError("無法連上WebAPI伺服器-"+err.message);
        }
      );
    });
  }

  get_max_exec_seq() {
    this.global.createLoader("取得最大值中...");
    this.global.loading.present().then(() => {
      this.ForsServices.GetMaxExecSeqAsync(
        this.item.exec_group
      ).subscribe(
        data => {
          if (data.DidError === true) {
            this.global.dismissLoading();
            this.global.showError(data.ErrorMessage);
          } else {
            this.item.exec_seq = data.Model;
            this.global.dismissLoading();
          }
        },
        err => {
          this.global.dismissLoading();
          this.global.showError("無法連上WebAPI伺服器-"+err.message);
        }
      );
    });
  }
  Help() {
    this.global.createLoader();

    this.global.loading.present().then(() => {
      this.global.showMessage("SQL語法說明", this.sql_help);
    });
  }
  DateLoopHelp() {
    this.global.createLoader();

    this.global.loading.present().then(() => {
      this.global.showMessage("日期迴圈SQL語法說明", this.date_sp_help);
    });
  }
  Save() {
    if(this.item.date_sp_name.length===0 && this.item.date_sql_statement.length===0)
    {
      this.global.showError("日期迴圈store procedure與日期迴圈sql語法必須二擇一輸入");
      return;
    }
    if(this.item.sp_name.length===0 && this.item.sql_statement.length===0)
    {
      this.global.showError("store procedure與sql語法必須二擇一輸入");
      return;
    }
    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }
  ionViewDidLoad() {
    console.log("ionViewDidLoad SqlModalPage");
  }
}



