import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { ForsListResponse } from "../../Model/Response/ForsListResponse";
import { ForsResponse } from "../../Model/Response/ForsResponse";
import { ForsViewModel } from '../../Model/ViewModel/ForsViewModel';
import { StringViewModel } from "../../Model/ViewModel/StringViewModel";
import { TableSetServicesProvider } from '../table-set-services/table-set-services';
/*
  Generated class for the ForsServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class ForsServicesProvider  extends TableSetServicesProvider {
  private Forss: ForsListResponse;
  private Fors: ForsResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    super(Services);
    this.ctl = "Fors";
    console.log("Hello ForsServiceProvider Provider");
  }

  public GetListsByAsync(PageSize: number, PageNumber: number, keyword: string,order_type:boolean) {
    var sub_url: string;
     sub_url = "Fors?PageSize={1}&PageNumber={2}&keyword={3}&order_type={4}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.Forss = new ForsListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.Forss;
    });
  }



  public PostPutAsync(ForsViewModel:ForsViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
      sub_url = "Fors";
    else
    sub_url = "Fors/{1}";
    sub_url = String.Format(sub_url, this.ctl,ForsViewModel.exec_table_seq);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,ForsViewModel,mode).map(
      item => {
        this.Fors = new ForsResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });

        return this.Fors;
      }
    );
  }

  public ExecuteAsync(StringViewModel:StringViewModel) {
    var sub_url: string;
    sub_url = "Fors/Execute";
    return this.Services.PostAsync(sub_url, this.ctl, true,StringViewModel).map(
      item => {
        this.Fors = new ForsResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });

        return this.Fors;
      }
    );
  }


  public DeleteAsync(exec_table_seq:string) {
    var sub_url: string;
    sub_url = "Fors/{1}";
    sub_url = String.Format(sub_url, this.ctl,exec_table_seq);

    return this.Services.DeleteAsync(sub_url, this.ctl, true).map(
      item => {
        this.Fors = new ForsResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.Fors;
      }
    );
  }





}



